            <div v-if="!support_type_id" class="text-md text-red-500 py-1 rounded-md text-md">At first please choose <strong class="text-lg uppercase">service type</strong>  you need! </div>
            <div class="bg-gray-200 px-4 py-3 gap-2 flex  md:justify-around items-center md:mx-auto rounded mt-2"> 
