        </div>
    </div>
    <div>
    <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="toggleModel">
        <div class="relative p-4 w-full max-w-2xl h-full md:h-auto" v-if="isLoggedIn && support_type_id && chamber && scheduleId">
            <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                <div class="modal-body relative p-4">
                    <div class="text-right">
                         <button class="-mt-2 mb-2 text-center px-2 text-red-500" @click="toggleModel=false">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                                </svg>                                            
                            </button>
                    </div>
                    <div class="bg-brand p-2 text-2xl rounded-md text-white">
                        Appointment Confirmation
                    </div>
                    <div class="flex flex-col justify-center items-center gap-2">
                        <div class="flex">
                            <label for="" class="">Chamber : </label>
                            <div v-html="chamber" class="text-md ml-2"></div>
                        </div>
                        <div class="flex">
                            <label for="" class="col-span-1">Date : </label>
                            <span class="text-md ml-2" v-html="dates"></span>
