                        <div class="flex">
                            <label for="">Time : </label>
                            <span v-html="stimeEtime" class="text-md ml-2"></span>
                        </div>
                        <div class="flex justify-between items-center mb-2 w-full overflow-hidden">
                            <label for="" class="mr-2 hidden md:block">Request Mentor(optional) : </label>
                           <Select class="border text-md overflow-scroll" v-model="requested_mentor_id">
                                <option value=""> Select Your Mentor </option>
                                <option :value="mentor.id" v-for="mentor in mentors" :key="mentor.id">
                                    <span class="text-sm">{{ mentor.name }}</span>
                                </option>
                           </Select>
                        </div>
                    </div>
                    <div>
                        <div class="flex justify-between items-center mb-2"  v-for="(question, index) in questions" :key="index">
                            <InputTextArea
                                class="w-4/5 py-2 p-2 border focus:outline-none"
                                v-model="questions[index]"
                                placeholder="Please share your problem with us to make the counselling session best effective. Your all informations will be kept confidential."
                                rows="4"
                            />
                            <button class="w-2/12 text-center px-2 text-red-500" @click="remove(index)">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                                </svg>                                            
                            </button>
                        </div>
                    </div>
                    <div class="text-left">
                            <button type="button" @click="addQuestions()" 
                            class="p-2 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white border border-blue-500 hover:border-transparent rounded">
                                    Add More Question
                            </button>
                    </div>
                </div>
                <div>
                   
                </div>
                <div class="modal-footer flex flex-shrink-0 flex-wrap items-center justify-between p-4 border-t border-gray-200 rounded-b-md">
                   <div>
                        <input type="checkbox" v-model="trems_conditions">
                        <span> I agree to the <a href="trems-and-conditions" class="cursor-pointer hover:underline text-brand" target="_blank">Terms and Conditions</a></span>
                   </div>
                    <button
                    type="button"
                    @click="postData()"
