                    </div>
                </div>
                </div>
        </div>
        <div v-if="isLoggedIn && !date">
                <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                <div class="w-2xl">
                    <button type="button" class="float-right text-red-600 rounded-full h-8 w-8  transition duration-100 ease-in-out" @click="toggleModel=false">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                            <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                        </svg>
                    </button>
                    <div class="text-xl text-red-500 p-4">
                        <span>Please Select Your Suitable Date </span> 
                    </div>
                </div>
                </div>
        </div>
        <div v-if="isLoggedIn && !chamberId && date">
                <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                <div class="w-2xl">
                    <button type="button" class="float-right rounded-full text-red-600  transition duration-100 ease-in-out" @click="toggleModel=false">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                            <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                        </svg>
                    </button>
                    <div class="text-xl text-red-500 p-4">
                        <span>Please Choose Chamber Location</span> 
                    </div>
                </div>
                </div>
        </div>
        <div v-if="isLoggedIn && chamberId && date && !scheduleId">
                <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                <div class="w-2xl">
                    <button type="button" class="float-right rounded-full text-red-600  transition duration-100 ease-in-out" @click="toggleModel=false">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
