    <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="isNotLogined">
        <div v-if="!isLoggedIn && suportType">
            <div class="w-full wax-w-2xl rounded shadow-2xl flex flex-col">
                <div class="max-w-4xl">
                    <div class="w-full bg-yellow-500 text-2xl text-white p-6">
                        <div class="p-5 md:text-4xl">Please Join First To Create An Appointment </div> 
                        <br>
                        <router-link class="bg-green-600 rounded-md text-white p-2 text-xl uppercase" to="/join">Go To Join </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="absolute inset-0 z-40 opacity-25 bg-gray-400" v-if="toggleModel"></div>
    </div>
</template>

<script>
import Btn from '../../../components/btn.vue';
import Select from '../../../components/select.vue';
import InputText from '../../../components/input-text.vue';
import InputTextArea from '../../../components/input-text-area.vue';
import Spinner from '../../../components/spinner.vue';
import { Calendar, DatePicker } from 'v-calendar';
import 'v-calendar/dist/style.css';
import axios,{isLoggedIn} from "../../../store/axios";
export default {
    components: { 
        Btn,
        InputText, 
        Spinner, 
        Btn,
        InputTextArea,
        DatePicker,
        Calendar,
        Select,

    },
    data() {
        return {
            toggleModel:false,
            centertoggleModel:false,
            historyModel:false,
            isNotLogined:false,
            chamberId: '',
            date: '',
            scheduleId: '',
            txt:'',
            user_id: this.$store.getters["auth/user"].id,
            questions: [''],
            type: '',
            requested_mentor_id: '',
            trems_conditions:false,
            support_type_id:'',
            slot: this.chamberSlot ? chamberSlot.slot_remains: 0 ,
            mentorId:'',
            dateTime:'',
        }
    },
    
     created() {

        this.$store.dispatch('schedule/getPreviousHistory');
        this.$store.dispatch('schedule/getSchedules');

     },

    watch: {
        date(newDate, oldDate) {
            this.searchSchedule();
        },
        chamberId(newChamberId, oldChamberId) {
            this.searchSchedule();
        },
        dateTime(newChamberId, oldChamberId) {
            this.searchSchedule();
        },
        
    },

    computed:{
       
        schedules(){
            return this.$store.state.schedule.schedules;
        },
         mentors(){
            return  this.schedules.mentors ? this.schedules.mentors[this.support_type_id] : '';
        },
        suportType(){
            const type = this.$store.state.schedule.previousHistory;
            return type.support_types;
        },
        availableDates() {
            const schedules = this.schedules;
            return schedules.dates ? Object.values(schedules.dates).map(date => date.status ? date.date : '') : [];
        },
        lastAppointments(){
            return this.$store.state.schedule.previousHistory;
        },
        availableChamber() {
            const schedules = this.schedules
            return schedules.chambers;
        },
        chamber(){
            const chambers = Object.values(this.availableChamber).filter(ch=>ch.id == this.chamberId);
            const chamber =  Object.values(chambers).map(chamber => chamber.name);
            return chamber;
        },
        chamberSlot(){
            if(this.date){
                const slot = this.schedules.chambers || '';
                const found = slot.filter(element => element.status !== null);
                if(found[0]){
                    return found[0].status || '';
                }
            }
        },
        availableTime() {
            const schedules = this.$store.state.schedule.schedules
            return schedules.schedules ? Object.values(schedules.schedules).map(schedule => schedule) : [];
        },

        time(){
            const times = Object.values(this.availableTime).filter(ch=>ch.id == this.scheduleId);
            console.log('asdfase',this.scheduleId);
            const time =  Object.values(times).map(time => time.time_schedule)  
            return time;
        },

        stimeEtime(){
            const stime =  Object.values(this.time).map(stime => stime.start_time);
            const etime =  Object.values(this.time).map(stime => stime.end_time);
            return `${stime} - ${etime}`;

        },
         attrs(){
            const schedules = this.$store.state.schedule.schedules
            const dates = schedules.dates ? Object.values(schedules.dates).map(date => date.status ? date.date : '') : [];
            return [
                {
                    highlight: {
                        color: 'orange',
                        fillMode: 'light',
                        contentClass: 'italic',
                    },

                    //     popover: {
                    //     label: 'This date you can Booked!',
                    //     visibility: 'hover',
                    //     hideIndicator: true,
                    // },
              
                    dates: dates,
                }
            ];
        },
        dates: {
            get() {
                return this.formatDate(this.date)
            }
        },
        isLoggedIn() {
            return  this.$store.getters["auth/authenticated"];
        },
    },

    methods: {

        async postData(){
            await axios().post('/api/appointments', {
                schedule_id: this.scheduleId,
                type:  this.support_type_id,
                user_id: this.user_id,
                questions: this.questions,
                requested_mentor_id: this.requested_mentor_id,
                trems_conditions: this.trems_conditions

            }).then( () => {
                this.$toast.success(`Your appointment is successfully submitted !!!`,{
                   position: "top",
                   duration:3000
                });
                this.$router.push('/my/my-profile');
            });
            
        },

        searchSchedule() {
            this.$store.dispatch('schedule/getSchedules', {
                support_type_id:this.support_type_id,
                chamber_id: this.chamberId || '',
                date: this.date ? this.formatDate(this.date) : '',
                scheduleId: this.scheduleId || '',
            });
        },


        formatDate(input) {

            let date = new Date(input);
            let month = '' + (date.getMonth() + 1);
            let day = '' + date.getDate();
            let year = date.getFullYear();

            if (month.length < 2) {
                month = '0' + month;
            }

            if (day.length < 2) {
                day = '0' + day;
            }
                
            return [year, month, day].join('-');
        },
        
        addNewField(){
            this.questions.push('');
        },

        removeField(index){
            this.questions.splice(index, 1)
        },

        addQuestions() {
            this.questions.push("");
        },
        
        remove(index){
            this.questions.splice(index, 1);
        },
        onChange(){

            this.date = '';
            this.chamberId = '';
            this.scheduleId = '';

            this.$store.dispatch('schedule/getSchedules',{
                support_type_id:this.support_type_id,
            });

        },
        chamberToggol(){
            this.chamberId = '';
            this.scheduleId = '';
        },
        isNotLoginedIn(){
            if(!this.isLoggedIn && this.support_type_id){
                this.isNotLogined = !this.isNotLogined;
            }
        }
        
        
    }
}
</script>